* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --breakpoint-small: 23.4375em;
  --breakpoint-large: 90em;
  --primary-soft-blue-color: #8bacda;
  --primary-cyan-color: #00fff7;
  --dark-blue-background-color: #0d192b;
  --dark-blue-card-color: #14253d;
  --dark-blue-line-color: #2f415b;
  --white-color: #fff;
  --card-width: 20.4375rem;
  --card-height: 33.9375rem;
  --card-border-radius-size: 1rem;
  --illustration-border-radius-size: .8rem;
  --heading-size: 1.375rem;
  --heading-line: 1.75rem;
  --paragraph-size: 1.125rem;
  --paragraph-line: 1.625rem;
  --detail-size: .9375rem;
  --detail-line: 1.1875rem;
  --content-size: 17.375rem;
  --avatar-size: 2.0625rem;
  --currency-block-size: 5.53rem;
  --duration-block-size: 5.79313rem;
}

body {
  background-color: var(--dark-blue-background-color);
  font-family: Outfit, sans-serif;
}

h1 {
  color: var(--white-color);
  font-weight: 600;
  font-size: var(--heading-size);
  line-height: var(--heading-line);
}

p {
  color: var(--primary-soft-blue-color);
  font-weight: 300;
  font-size: var(--paragraph-size);
  line-height: var(--paragraph-line);
}

.currency > *, .duration > *, .creator > * {
  font-weight: 400;
  font-size: var(--detail-size);
  line-height: var(--detail-line);
}

.currency__value {
  color: var(--primary-cyan-color);
  font-weight: 600;
}

.container {
  width: 100vw;
  height: 100vh;
  min-width: var(--breakpoint-small);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.card {
  border-radius: var(--card-border-radius-size);
  width: var(--card-width);
  height: var(--card-height);
  background-color: var(--dark-blue-card-color);
  min-height: var(--card-height);
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

.card__illustration, .card__heading, .card__paragraph, .card__detail, .card__cover, .creator {
  width: var(--content-size);
}

.card__illustration {
  border-radius: var(--illustration-border-radius-size);
  height: var(--content-size);
  position: relative;
}

.card__illustration:hover {
  background: var(--primary-cyan-color);
  cursor: pointer;
}

.card__cover {
  border-radius: var(--illustration-border-radius-size);
}

.card__cover:hover {
  opacity: .4;
}

.card__cover:hover + .card__eye--hover {
  display: block;
}

.card__eye--hover {
  margin: auto;
  display: none;
  position: absolute;
  inset: 0;
}

.card__heading:hover, .creator strong:hover {
  color: var(--primary-cyan-color);
  cursor: pointer;
}

.card__detail, .currency, .duration {
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.currency {
  flex-basis: var(--currency-block-size);
}

.duration {
  flex-basis: var(--duration-block-size);
}

.card__divider {
  height: 1px;
  width: var(--content-size);
  background-color: var(--dark-blue-line-color);
  border: none;
}

.creator {
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
  display: flex;
}

.creator strong {
  color: var(--white-color);
  font-weight: 400;
}

.creator__avatar {
  width: var(--avatar-size);
  height: var(--avatar-size);
  border: 1px solid var(--white-color);
  border-radius: 1rem;
}

.attribution {
  text-align: center;
  margin: 2rem 0;
  padding: .4rem 0;
}

.attribution a {
  color: var(--white-color);
  text-decoration: none;
}

.attribution a:hover {
  text-decoration: underline;
}

@media screen and (width >= 90em) {
  :root {
    --card-width: 21.875rem;
    --card-height: 37.25rem;
    --content-size: 18.875rem;
    --heading-size: 1.375rem;
    --heading-line: 1.75rem;
    --paragraph-size: 1.125rem;
    --paragraph-line: 1.625rem;
    --detail-size: 1rem;
    --detail-line: 1.25rem;
    --currency-block-size: 5.8425rem;
    --duration-block-size: 6.04375rem;
  }
}

